.typewriter-container {
    font-size: 2rem;
    color: #333;
    white-space: nowrap;
    overflow: hidden;
    border-right: 0.15em solid orange; /* The cursor */
    animation: blink-caret 0.75s step-end infinite;
}

@keyframes blink-caret {
    from, to {
        border-color: transparent;
    }
    50% {
        border-color: orange;
    }
}
